.advices__title {
  @include main-text(18px, 26px, bold);
  text-transform: uppercase;
  margin: 0;
  padding: 25px 0 10px 0;
}

.advices__description {
  @include main-text(14px, 21px, normal);
  color: #333333;
  text-transform: uppercase;
  margin: 0;
  padding-top: 6px;
}

.advices__item {
  display: flex;
  flex-shrink: 0;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}

.advices__text {
  margin-left: 6px;
  padding: 0 4px;
  margin-bottom: 0;
}

.advices__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.advices__img {
  fill: $base-block-color;
}

.advices__wrapper {
  margin: 0 auto;
  padding: 10px 20px 36px 20px;
}

@media (max-width: $mobile-width-only) {
  .advices__br-not-mobile {
    display: none;
  }

  .advices__br-only-desktop {
    display: none;
  }
}

@media (min-width: $tablet) {
  .advices__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 0 auto;
  }

  .advices__item--apartment {
    grid-column-start: 1;
    grid-row-start: 1;
  }

  .advices__item--gifts {
    grid-column-start: 3;
    grid-row-start: 1;
  }

  .advices__item--meal {
    grid-column-start: 2;
    grid-row-start: 1;
  }

  .advices__item {
    display: block;
    text-align: center;
  }

  .advices__title {
    @include main-text(21px, 26px, bold);
    margin-bottom: 8px;
    padding-top: 12px;
  }

  .advices__description {
    padding-left: 28px;
    padding-right: 28px;
  }

  .advices__text {
    margin: 0;
    margin-top: 10px;
  }

  .advices__br-only-desktop {
    display: none;
  }

  .advices__wrapper {
    padding: 60px 0 60px 0;
  }
}

@media (min-width: $desktop) {
  .advices__wrapper {
    padding: 60px 0 78px 0;
  }

  .advices__title {
    @include main-text(21px, 26px, bold);
    margin-bottom: 8px;
  }

  .advices__text {
    margin: 0;
    margin-top: 12px;
    padding: 0;
    padding-left: 40px;
    padding-right: 40px;
  }

  .advices__description {
    padding-left: 18px;
    padding-right: 18px;
  }

  .advices__br-only-desktop {
    display: inline;
  }
}
