.hotels__title {
  @include main-text(21px, 26px, bold);
  text-transform: uppercase;
  text-align: center;
}

.hotels__action {
  @include main-text(14px, 24px, normal);
  color: #333333;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 27px;
}

.hotels__button-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.hotels__button {
  width: 280px;
}

.hotels__click {
  margin: 0 auto;
  margin-top: 33px;
  margin-bottom: 30px;
}

.hotels__wrapper {
  margin: 0 auto;
  padding: 0 20px 20px 20px;
}

@media (max-width: $mobile-width-only) {
  .hotels__br-not-mobile {
    display: none;
  }
}

@media (min-width: $tablet) {
  .hotels__title {
    @include main-text(30px, 26px, bold);
    margin-bottom: 33px;
  }

  .hotels__button {
    width: 458px;
  }

  .hotels__click {
    margin: 0 auto;
    margin-top: 58px;
    margin-bottom: 0;
  }

  .hotels__action {
    margin-bottom: 38px;
  }

  .hotels__wrapper {
    margin: 0 auto;
    padding-left: 140px;
    padding-right: 140px;
    padding-bottom: 58px;
  }
}

@media (min-width: $desktop) {
  .hotels__wrapper {
    padding-left: 270px;
    padding-right: 270px;
    padding-bottom: 46px;
  }

  .hotels__action {
    margin-bottom: 42px;
  }

  .hotels__click {
    margin-bottom: 20px;
  }

  .hotels__title {
    margin-top: 28px;
    margin-bottom: 32px;
  }
}
