.feature__point,
.feature__description,
.feature__number {
  color: #ffffff;
  text-transform: uppercase;
}

.feature__point {
  @include main-text(18px, 26px, bold);
  margin-bottom: 10px;
}

.feature__number {
  margin-bottom: 18px;
}

.feature__description,
.feature__number {
  @include main-text(14px, 21px, normal);
}

.feature__text {
  background-color: $base-block-color;
  padding: 34px 12px 38px 12px;
  text-align: center;
}

.feature__text-another-size {
  padding: 40px 12px 36px 12px;
}

.feature__image {
  background-color: #ffffff;
}

.feature__img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 260px;
}

.feature__wrapper {
  margin: 0 auto;
}

@media (max-width: $mobile-width-only) {
  .feature__br-not-mobile {
    display: none;
  }
}

@media (min-width: $tablet) {
  .feature__items {
    display: grid;
    grid-template-columns: 33.33% 66.67%;
    margin: 0 auto;
  }

  .feature__items--order {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }

  .feature__text--place {
    grid-column-start: 2;
    grid-row-start: 1;
  }

  .feature__image--place {
    grid-column-start: 1;
    grid-row-start: 1;
  }

  .feature__point {
    @include main-text(21px, 26px, bold);
    margin-bottom: 12px;
  }

  .feature__img {
    height: 256px;
  }

  .feature__text {
    padding: 40px 32px 44px 32px;
  }

  .feature__number {
    margin-bottom: 24px;
  }

  .feature__text-another-size {
    padding: 42px 36px 40px 34px;
  }
}

@media (min-width: $desktop) {
  .feature__text {
    padding: 48px 60px 44px 60px;
  }

  .feature__text-another-size {
    padding-top: 50px;
  }

  .feature__description-another-size {
    padding-left: 30px;
    padding-right: 30px;
  }

  .feature__point {
    padding-left: 50px;
    padding-right: 50px;
  }
}
