.intro-c__title {
  @include main-text(18px, 26px, bold);
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 14px;
}

.intro-c__description {
  @include main-text(14px, 24px, normal);
  text-transform: uppercase;
  color: #333333;
  text-align: center;
}

.intro-c__view {
  background: #5f89b9 url("../img/backmobileForm.jpg") no-repeat center/cover;
  min-height: 158px;
  position: relative;

  @media (min-resolution: 2dppx) {
    background: #5f89b9 url("../img/backmobileForm2x.jpg") no-repeat center/cover;
  }
}

.intro-c__text-index {
  width: 229px;
  margin: 0 auto;
  position: absolute;
  left: 50%;
  top: 53px;
  transform: translateX(-50%);
}

.intro-c__text-index-img {
  width: 229px;
  height: 39px;
}

.intro-c__dec-triangle {
  background: url("../img/introtriangledesktop.svg") no-repeat center center;
  position: absolute;
  height: 57px;
  bottom: 0;
  left: 0;
  right: 0;
}

.intro-c__text {
  padding: 18px 16px 18px 16px;
}

.intro-c__wrapper {
  margin: 0 auto;
  position: relative;
}

@media (max-width: $mobile-width-only) {
  .intro-c__wrapper::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #e5e5e5;
  }
}

@media (min-width: $tablet) {
  .intro-c__title {
    @include main-text(30px, 26px, bold);
    padding-bottom: 8px;
  }

  .intro-c__view {
    background: #5f89b9 url("../img/backtabletForm.jpg") no-repeat center/cover;
    min-height: 290px;

    @media (min-resolution: 2dppx) {
      background: #5f89b9 url("../img/backtabletForm2x.jpg") no-repeat center/cover;
    }
  }

  .intro-c__text-index {
    width: 457px;
    margin: 0 auto;
    position: absolute;
    left: 50%;
    top: 95px;
    transform: translateX(-50%);
  }

  .intro-c__text-index-img {
    width: 457px;
    height: 78px;
  }

  .intro-c__text {
    padding: 38px 165px 64px 165px;
  }
}

@media (min-width: $desktop) {
  .intro-c__view {
    background: #5f89b9 url("../img/backdesktopForm.jpg") no-repeat center/cover;
    min-height: 301px;

    @media (min-resolution: 2dppx) {
      background: #5f89b9 url("../img/backdesktopForm2x.jpg") no-repeat center/cover;
    }
  }

  .intro-c__text-index {
    top: 101px;
  }

  .intro-c__text {
    padding: 74px 381px 67px 381px;
  }
}
